@keyframes scanline {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(100%);
  }
}

.scanline {
  animation: scanline 2s linear infinite;
}

/* CRT screen effect */

.crt-effect {
  position: relative;
  overflow: hidden;
}

.crt-effect::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%);
  background-size: 100% 4px;
  pointer-events: none;
  z-index: 10;
}

